<template>
  <div id="main-container">
    <table class="responsive">
      <caption>
        {{ $route.meta.title }}
      </caption>
      <thead>
      <tr>
        <th scope="col">이름</th>
        <th scope="col">타입</th>
      </tr>
      </thead>
      <tbody>
      <tr>
        <td data-label="이름">
          {{ figureUpdateForm.figureName }}
        </td>
        <td data-label="타입">
          <input-select
            name="type"
            disabled="disabled"
            :options="types"
            v-model="figureUpdateForm.type"
            class="select-box"/>
        </td>
      </tr>
      </tbody>
    </table>
    <table class="responsive">
      <caption>
        제작정보
      </caption>
      <thead>
      <tr>
        <th scope="col">시작일자</th>
        <th scope="col">마감일자</th>
        <th scope="col">완료여부</th>
      </tr>
      </thead>
      <tbody>
      <tr>
        <td
          data-label="시작일자">
          <DatePicker
            name="startDate"
            v-model="figureUpdateForm.startDate"
            placeholder="시작일자"
          />
        </td>
        <td
          data-label="마감일자">
          <DatePicker
            name="endDate"
            v-model="figureUpdateForm.endDate"
            placeholder="마감일자"
          />
        </td>
        <td data-label="완료여부">
          <input-select
            name="completeYn"
            :options="completes"
            v-model="figureUpdateForm.completeYn"
            class="select-box"/>
        </td>
      </tr>
      </tbody>
    </table>
    <div class="input-wrapper" style="margin: 10px 0 10px 0;">
      <button
        @click="_updateFigure"
        class="basic__button"
      >
        수정하기
      </button>
      <button
        class="cancel__button"
        @click="_routeToGunplaProgressList"
      >
        뒤로가기
      </button>
    </div>
  </div>
</template>

<script>
import DatePicker from '@/components/date-picker.vue'
import { mapState } from 'pinia'
import { usersStore } from '@/store/users'
import { figureStore } from '@/store/figures'
import Constants from '@/Constants'
import sweetAlert from '@/wrapper/sweet-alert'
import jwtDecode from 'jwt-decode'

export default {
  name: 'user-gunpla-progress-detail',
  components: { DatePicker },
  computed: {
    ...mapState(usersStore, ['userCustomInfo', 'loggedInAccessToken']),
    ...mapState(figureStore, ['getFigureProgressInfo'])
  },
  data () {
    return {
      types: Constants.TYPES,
      grades: Constants.GRADES,
      completes: Constants.Completes,
      figureUpdateForm: {
        figureOid: 0,
        figureName: null,
        type: { code: '', value: '' },
        wishYn: false,
        nickName: null,
        version: null,
        series: null,
        grade: { code: '', value: '' },
        generation: null,
        price: 0,
        date: null,
        store: null,
        startDate: null,
        endDate: null,
        completeYn: { code: '', value: '' }
      }
    }
  },
  methods: {
    _routeToGunplaProgressList () {
      this.$router.push({ name: 'user-gunpla-progress-list' })
    },
    _updateFigure () {
      if (this.figureUpdateForm.figureName.replace(/ /g, '') === '') {
        sweetAlert.noIcon('이름은 필수 입력 사항입니다.', '확인')
      } else {
        const figureUpdateIn = {
          figureOid: this.figureUpdateForm.figureOid,
          figureName: this.figureUpdateForm.figureName,
          type: this.figureUpdateForm.type,
          wishYn: this.figureUpdateForm.wishYn,
          nickName: this.figureUpdateForm.nickName,
          version: this.figureUpdateForm.version,
          series: this.figureUpdateForm.series,
          grade: this.figureUpdateForm.grade,
          generation: this.figureUpdateForm.generation,
          price: String(this.figureUpdateForm.price).replaceAll(',', ''),
          date: this.figureUpdateForm.date,
          store: this.figureUpdateForm.store,
          startDate: this.figureUpdateForm.startDate,
          endDate: this.figureUpdateForm.endDate,
          completeYn: this.figureUpdateForm.completeYn
        }
        this.updateFigure(this.userCustomInfo.userOid, figureUpdateIn).then(() => {
          sweetAlert.noIcon(`${figureUpdateIn.figureName} 수정 되었습니다 :)`)
          this._routeToGunplaProgressList()
        })
      }
    }
  },
  created () {
    if (this.userCustomInfo === undefined) {
      sweetAlert.noIcon('잘못된 경로로 진입하셨거나 인증 정보가 없습니다. 로그인 화면으로 이동합니다.', '확인')
      this.logout()
    } else {
      if (new Date(jwtDecode(this.loggedInAccessToken).exp * 1000) <= new Date()) {
        sweetAlert.noIcon('자동 로그인 기간이 만료 되었습니다. 다시 로그인 해 주세요 :)', '확인')
        this.logout()
      } else {
        this.figureUpdateForm = this.getFigureProgressInfo
      }
    }
  },
  mounted () {
    this.figureUpdateForm.type = this.getFigureProgressInfo.type.code
    this.figureUpdateForm.grade = this.getFigureProgressInfo.grade.code
    this.figureUpdateForm.completeYn = this.getFigureProgressInfo.completeYn.code
    this.figureUpdateForm.price = String(this.figureUpdateForm.price)
      .replace(/^0+|\D+/g, '').replace(/(\d)(?=(?:\d{3})+(?!\d))/g, '$1,')
  }
}
</script>
<style scoped lang="scss">
.grid-item {
  height: 160px !important;
}
.button__wrapper {
  margin-top: 30px;
  text-align: right;
}
</style>
